import { css, customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { UMB_MODAL_MANAGER_CONTEXT } from '@umbraco-cms/backoffice/modal';
import { JUMOO_PROCESSING_SIDEBAR_MEDIUM_MODAL } from '@jumoo/processing';
import uSyncExporter from '../../constants';

@customElement('usync-exporter-import-view-element')
export class uSyncExporterImportViewElement extends UmbLitElement {
	async #openImportDialog() {
		const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
		if (!modalManager) return;

		const importModal = modalManager.open(this, JUMOO_PROCESSING_SIDEBAR_MEDIUM_MODAL, {
			data: {
				processor: uSyncExporter.process.pipeline,
				strategy: uSyncExporter.process.import,
				progressLabel: 'Import',
				options: {
					$type: 'ExporterSyncPackProcessingOptions',
				},
				headline: 'Import Sync-Pack',
			},
		});

		const data = await importModal.onSubmit().catch((_rejected) => {
			return false;
		});

		if (!data) return;
	}

	render() {
		return html` <umb-body-layout>
			<uui-box .headline=${this.localize.term('uSyncExport_import')}>
				<div>
					<umb-localize key="uSyncExport_importIntro"></umb-localize>
				</div>

				<uui-button
					label="Import"
					color="default"
					look="primary"
					@click="${this.#openImportDialog}"></uui-button>
			</uui-box>
		</umb-body-layout>`;
	}

	static styles = css`
		uui-button {
			margin-top: 10px;
		}
	`;
}

export default uSyncExporterImportViewElement;
